@use "../../variables" as var;
.home {
  position: relative;
  .balanceInfo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  .mainContentWrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    .mainIntro {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border-radius: 20px;
      gap: 20px;
      padding: 15px 20px;
      padding-left: 0;
      .introWrapper {
        display: flex;
        flex-direction: column;
        .introContent {
          margin-bottom: 20px;
          font-size: 16px;
          text-align: left;

          font-weight: 400;
          line-height: 16px;
          text-transform: capitalize;
          color: rgba(#fff, 0.5);

          span {
            line-height: 20px;
            color: var.$color-content-text-yellow;
          }
        }
        > p {
          text-align: left;
          font-weight: 400;
          font-size: 16px;
          line-height: 20px;
          color: #ffffff;
        }
      }
    }
    .plans {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      gap: 10px;
    }
    .userInfo {
      .referralWrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
        gap: 10px;
        .stakeSummary {
          flex: 1;
          width: 100%;
          background: var.$color-content-background;
          border-radius: 20px;
          padding: 25px;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          align-items: center;
          row-gap: 15px;
          > img {
            width: 70%;
          }

          > h6 {
            font-weight: 400;
            font-size: 16px;
            line-height: 16px;
            text-transform: capitalize;
            color: #ffffff;
            margin-bottom: 0;
          }
          > span {
            display: block;
            font-weight: 700;
            font-size: 45px;
            line-height: 45px;
            text-transform: capitalize;
            color: var.$color-content-text-yellow;
          }
          .withDrawButton {
            width: 50%;
            background: var.$color-primary-yellow;
            border-radius: 8px;
            text-align: center;
            margin: 0 auto;
            margin-top: 20px;
            padding: 10px;

            font-weight: 700;
            font-size: 20px;
            line-height: 20px;
            text-transform: capitalize;
            color: #151414;

            cursor: pointer;
          }
        }
        .referralDetail {
          flex: 1;
          width: 100%;
          background: var.$color-content-background;
          border-radius: 20px;
          padding: 25px;

          h6 {
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            text-transform: capitalize;
            color: #ffffff;
            opacity: 0.5;
            text-align: left;
            margin-top: 10px;
          }

          span {
            display: block;
            font-weight: 700;
            font-size: 45px;
            line-height: 45px;
            text-transform: capitalize;
            color: var.$color-content-text-yellow;
            text-align: left;
          }
          .referralLink {
            background: #151414;
            border-radius: 8px;
            padding: 10px 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;
            > h6 {
              margin: 0;
              font-size: 12px;
            }
            > img {
              width: 20px;
            }
          }
        }
        .referralBonus {
          text-align: left;
          margin-top: 20px;
          font-weight: 400;
          font-size: 16px;
          line-height: 18px;
          color: var.$color-primary-yellow;
          opacity: 0.5;
        }
      }
    }
  }
}

.common {
  background-color: var.$color-content-background;
}

@media (max-width: 765px) {
  .home {
    .balanceInfo {
      flex-direction: column;
      gap: 20px;
    }
    .mainIntro {
      flex-direction: column;
      gap: 20px;
      .introWrapper {
        padding: 25px;
      }
      > img {
        display: none;
      }
    }
    .mainContentWrapper {
      .userInfo {
        .referralWrapper {
          flex-direction: column;
          .stakeSummary {
            // max-width: 100%;
            flex: none;
            width: 100%;
            gap: 10px;
            // > span {
            //   font-size: 40px;
            //   line-height: 40px;
            // }
            .withDrawButton {
              font-size: 16px;
            }
          }
          // .referralDetail {
          //   span {
          //     font-size: 40px;
          //     line-height: 40px;
          //   }
          // }
        }
      }
    }
  }
}
