.blue {
  .planTitle {
    .type {
      background-color: var.$color-primary-blue;
    }
    .state {
      color: var.$color-primary-blue;
    }
  }
  .infoItem {
    h1 {
      color: var.$color-primary-blue;
    }
  }
  .progress {
    .inner {
      background-color: var.$color-primary-blue;
    }
  }
}
.planTitle {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  p {
    margin: 0;
  }
  .type,
  .state {
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
  }
  .type {
    background-color: var.$color-primary-yellow;
    color: black;
    padding: 0 5px;
    border-radius: 5px;
  }
  .state {
    color: var.$color-primary-yellow;
  }
  .time {
    font-size: 16px;
    line-height: 16px;
    text-transform: capitalize;
    color: #ffffff;
    opacity: 0.5;
  }
}
.planDetail {
  // margin: 20px auto;
  // padding: 5px 25px;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.infoItem {
  display: flex;
  align-items: center;
  flex-direction: column;
  p {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    text-transform: capitalize;
    color: #ffffff;
    opacity: 0.5;
    text-align: left;
    margin: 0;
    margin-top: 2px;
  }

  h1 {
    display: block;
    font-weight: 700;
    font-size: 45px;
    line-height: 45px;
    text-transform: capitalize;
    text-align: left;
    color: var.$color-primary-yellow;
    margin: 0;
  }
  &.left {
    align-items: flex-start;
  }
  &.right {
    align-items: flex-end;
  }
}

