@use "../../../variables" as var;
.balanceCardWrapper {
  // height: 100%;
  width: 100%;
  background: var.$color-content-background;
  border-radius: 20px;
  padding: 15px 0;
  position: relative;
  text-align: left;
  padding-left: 20px;
  overflow-x: hidden;

  .colorBar {
    width: 8px;
    height: 60%;
    background: var.$color-primary-yellow;
    position: absolute;
    left: 0;
    top: 20%;
  }
  .cardContent {
    > h2 {
      color: white;
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 10px;
      > span {
        display: inline-block;
        background: var.$color-primary-yellow;
        color: #222222;
        font-weight: 700;
        padding: 2px 5px;
        font-size: 16px;
      }
    }
    > p {
      font-style: normal;
      font-weight: 700;
      font-size: 45px;
      line-height: 45px;
      text-transform: capitalize;
      color: var.$color-content-text-yellow;
      margin-bottom: 0;
    }
  }
}
