@use "../../variables" as var;
.header {
  position: relative;
  width: 100%;
  padding: 20px 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;

  .menuIcon {
    color: white;
    font-size: 24px;
    cursor: pointer;
  }
  .iconWrap {
    > img {
      height: 67px;
      width: 260px;
    }
  }

  .functionWrapper {
    display: flex;
    margin-left: auto;
    gap: 5px;
  }
  .walletButton {
    > div {
      border: 1px solid var.$color-content-text-yellow;
    }
  }
}
@media (max-width: 900px) {
  .header {
    padding: 10px 25px;
    padding-bottom: 0;
    > img {
      display: block;
      width: 35px;
      animation: updown 2s ease infinite;
    }
    .functionWrapper {
      display: flex;
      box-sizing: border-box;
      position: absolute;
      top: 60px;
      left: 0;
      transform: none;
      width: 100%;
      z-index: 1;
      background-color: rgba($color: black, $alpha: 0.7);
      row-gap: 10px;
      padding: 40px;
      flex-wrap: wrap;
      flex-direction: column;
    }
  }
}
@keyframes updown {
  0% {
    transform: translateY(-5%);
  }

  50% {
    transform: translateY(5%);
  }

  100% {
    transform: translateY(-5%);
  }
}
